<template>
  <div class="myCar">
    <van-popup v-model="showCalendar" position="bottom">
      <van-datetime-picker
        v-model="currentDate"
        type="date"
        title="选择年月日"
        @change="changeData"
        @cancel="showCalendar = false"
        @confirm="confirmData"
        :min-date="minDate"
        :max-date="maxDate"
        :formatter="formatter"
      />
    </van-popup>

    <div class="title">申领</div>
    <div class="carbox">
      <div class="caritem" v-for="i in 50" :key="i">
        <div class="imgbox">
          <van-image
  class="imgbox"
  fit="contain"
  src="https://img.yzcdn.cn/vant/cat.jpeg"
/>
        </div>
        <div class="infobox">5555</div>
      </div>
    </div>
    <div class="formbox">
      <van-form @submit="onSubmit">
        <div>
          <van-field
            readonly
            clickable
            name="calendar"
            :value="username"
            label="使用时间"
            placeholder=""
            @click="showCalendar = true"
            :rules="[{ required: true, message: '请填写使用时间' }]"
          />
          <van-field
            v-model="password"
            type="password"
            name="申请理由"
            label="申请理由"
            placeholder=""
            :rules="[{ required: true, message: '请填写申请理由' }]"
          />
        </div>

        <div style="display: flex">
          <van-button
            plain
            hairline
            type="primary"
            size="small"
            style="width: 100%"
            >提交表单</van-button
          >
        </div>
      </van-form>
    </div>
  </div>
</template>

<script>
import { Toast, Form, Field, DatetimePicker, popup,image } from "vant";

export default {
  name: "myCar",
  data() {
    return {
      username: "",
      password: "",
      showCalendar: false,
      minDate: new Date(2020, 0, 1),
      maxDate: new Date(2025, 10, 1),
      currentDate: new Date(),
    };
  },

  components: {
    [Form.name]: Form,
    [Field.name]: Field,
    [DatetimePicker.name]: DatetimePicker,
    [popup.name]: popup,
    [image.name]: image,
  },
  methods: {
    formatter(type, val) {
      if (type === "year") {
        return val + "";
      }
      if (type === "month") {
        return val + "";
      }
      if (type === "day") {
        return val + "";
      }
      return val;
    },
    onSubmit() {},
    changeData(val){
    //    this.currentDate = val;
    },
    timeFormat(time) {
      // 时间格式化 2019-09-08
      let year = time.getFullYear();
      let month = time.getMonth() + 1;
      let day = time.getDate();
      return year + "-" + month + "-" + day + "";
    },
    confirmData(val) {
    
      this.username = this.timeFormat(val);
           this.currentDate=  val;
      this.showCalendar = false;
    },
  },
};
</script>

<style lang="less" scoped>
.myCar {
  height: 100%;
  display: flex;
  flex-direction: column;
  .title {
    height: 30px;
    font-size: 16px;
    background: white;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  .carbox {
    flex: 1;
    background: #f2f3f5;
    overflow: auto;
    .caritem {
      min-height: 100px;
      max-width: 100%;
      padding: 10px;
      background: white;
      margin: 10px 10px;
      border-radius: 5px;
      display: flex;
      .infobox {
        flex: 1;
        margin: 0 0 0 10px;
        background: #f7f8fa;
        font-size: 12px;
        border-radius: 5px;
      }
      .imgbox {
        width: 80px;
        height: 80px;
        background: #f7f8fa;
        font-size: 12px;
        border-radius: 5px;
        overflow: hidden;
      }
    }
  }
  .formbox {
    /* border: 1px solid #ebedf0; */
    padding: 10px;
    border-radius: 10px;
    overflow: hidden;

    border-top: 1px solid #f2f3f5;
  }
  .btnbox {
    height: 40px;
    width: 100%;
    background: white;
    font-size: 16px;
    line-height: 40px;
    text-align: center;
  }
  /deep/ .imgbox.van-image{
      width:80px;
      height: 80px;
  }
}
</style>