<template>
  <div class="tabbar">
    <van-goods-action>
          <van-goods-action-icon
        icon="cart-o"
        text="直发"
        badge="5"
        @click="onClickIcon1"
      />
      <van-goods-action-icon
        icon="cart-o"
        text="申领"
        badge="5"
        @click="onClickIcon1"
      />
      <van-goods-action-icon
        icon="cart-o"
        text="申购"
        badge="5"
        @click="onClickIcon2"
      />

      <!-- <van-action-sheet v-model="show" title="标题">
           <div class="goods">

           </div>
      </van-action-sheet> -->
      <van-popup
        class="tabbar-popup"
        lock-scroll
        v-model="show1"
        position="bottom"
        @close="closepopup"
        closeable
        close-icon="close"
      >
        
        <mycar></mycar>   
      </van-popup>

      <!-- <van-popup
        lock-scroll
        v-model="show2"
        position="bottom"
        @close="closepopup"
        closeable
        close-icon="close"
      >
        <div class="car">
          <div class="caritem">
            <div>left</div>
            <div>right</div>
          </div>
        </div>
      </van-popup> -->

      <!-- <van-goods-action-button
        type="danger"
        text="立即购买"
        @click="onClickButton"
      /> -->
    </van-goods-action>
  </div>
</template>
<script>
import { GoodsAction, GoodsActionIcon, GoodsActionButton, Popup } from "vant";
import { ActionSheet } from "vant";
import delivery from "@/api/delivery/delivery";
import files from "@/api/files/files";
import { changetrueTo1, stopScroll, openScroll } from "@/utils/commonJs";
import { lockBackgroundScroll } from "@/utils/backgroundScrollOverly";
import mycar from "@/views/goods/compoments/myCar";

import { Toast, NavBar, TreeSelect, Stepper, checkbox, Uploader } from "vant";
var _self;
export default {
  name: "intervalTimeSelect",
  components: {
    [NavBar.name]: NavBar,
    [TreeSelect.name]: TreeSelect,
    [Stepper.name]: Stepper,
    [checkbox.name]: checkbox,
    [Uploader.name]: Uploader,
    [GoodsAction.name]: GoodsAction,
    [GoodsActionIcon.name]: GoodsActionIcon,
    [GoodsActionButton.name]: GoodsActionButton,
    [ActionSheet.name]: ActionSheet,
    [Popup.name]: Popup,
    mycar
  },
  props: {
    goodList: {
      type: Object,
      default: () => {
        return {};
      },
    },
  },
  data() {
    return {
      show1: false,
      show2: false,
    };
  },
  beforeUpdate() {},
  computed: {},
  created() {},
  mounted() {},

  methods: {
    stopScroll() {
      document.documentElement.style.overflow = "hidden";
    },
    onClickButton() {},
    onClickIcon() {
      stopScroll();
      this.show = true;
    },
    onClickIcon1() {
      this.$emit("hiddenSearch", false);
      stopScroll();
      this.show1 = true;
    },
    onClickIcon2() {
      this.$emit("hiddenSearch", false);
      stopScroll();
      this.show2 = true;
    },
    closepopup() {
      this.$emit("hiddenSearch", true);
      openScroll();
    },
  },
  /**
   * @Description: 过滤器函数
   * @Param:
   * @Author: xwq
   * @LastEditors: xwq
   * @LastEditTime: Do not edit
   * @return:
   * @Date: 2019-10-18 17:21:56
   */
  filters: {},
};
</script>
<style lang="less" scoped>
.tabbar {
  height: 50px;
  width: 100%;
  background: white;
  position: fixed;
  bottom: 0;
  left: 0;
  right: 0;
  z-index: 1;
   .tabbar-popup.van-popup--bottom {
  min-height: 100%;
  max-height: 100%;
  height: 100%;
}
}

/deep/ .van-goods-action {
  justify-content: flex-end;
}

/deep/ .van-popup__close-icon--top-right{
    top:4px;
    right: 8px;
}
</style>